import { useParams } from 'react-router-dom';
import { useLaunchDarkly } from '@/hooks/launchDarkly';
import EditChamber from './EditChamber';
import EditNoum from './EditNoum';

const Index = () => {
  const { id = '' } = useParams();
  const {
    flags: { noumEditor2 },
  } = useLaunchDarkly();

  return noumEditor2 ? <EditNoum id={id} /> : <EditChamber id={id} />;
};

export default Index;
